import React from 'react';
import ReactDom from 'react-dom';
import './index.css';
import Header from './Header';
import Card from './Card';
import Sdata from './Sdata';



ReactDom.render(
  <>
  <Header />
  {Sdata.map((val,index) =>{
    return(
      <Card  
    imgsrc = {val.imgsrc}
    title = {val.title}
    sname = {val.sname}
    link = {val.link}
  />
    )
  })}

  <footer>
        Copyright &copy;2020 by Mak Anime.
  </footer>


  </>,
  document.getElementById('root')
);

